<template>
    <div id="app" :style="'background-size: cover; background: ' + backgroundUrl">
        <router-view/>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        name: 'App',
        data () {
            return {
                backgroundUrl: ''
            }
        },
        computed: {
            ...mapGetters([
                'githubUsername',
                'htmlTitle'
            ])
        },
        created() {
            console.log('app created')
            this.$store.dispatch("Init")
            this.$store.dispatch("GetInfo")
            this.$setTitle(this.$route.meta.title)
            let windowSize = this.$util.getWindowSize()
            let pathArr = this.$route.path.split("/")
            if (pathArr[1] == "user" && windowSize.height > windowSize.width * 1.2) {
                this.$router.push("/mobile/user/blog")
            }
            if (pathArr[1] == "mobile" && windowSize.height <= windowSize.width * 1.2) {
                this.$router.push("/")
            }
        },
        mounted () {
            this.backgroundUrl = `url('/static/img/bg/bg6.jpg')`
        }
    }
</script>

<style lang="less">
body{
    overflow: hidden;
}
#app{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

</style>



