<template>
  <div style="min-height: 600px; background: white; border-radius: 4px;">
    <div @drop="drop">拖放到此处</div>
    <input type="file" @change="change">
    <el-button type="primary" @click="onSubmit" :loading="submitButton.loading" :disabled="submitButton.disabled">发表
    </el-button>
    <el-button @click="$router.push('/user/blog/main')">返回</el-button>
    <!--<div v-if="files&&files.length>0">-->
      <!--<el-collapse v-model="activeName" accordion>-->
        <!--<el-collapse-item v-for="(file, index) in files" :title="file.title" :name="index">-->
          <!--<el-button @click="del(index)" style="padding: 3px 0" type="text" icon="el-icon-delete"-->
                         <!--v-if="token"></el-button>-->
          <!--<div>{{file.description}}</div>-->
        <!--</el-collapse-item>-->
      <!--</el-collapse>-->
    <!--</div>-->
    <div v-if="files&&files.length>0">
      <el-card shadow="hover" v-for="(item,index) in files" :key="'p'+index"
               style="margin-bottom: 20px; height: 200px; overflow: hidden;"
               v-if="!item.hide">
        <div slot="header">
          <el-row>
            <el-col :span="23">
              <span>{{item.title}}</span>
            </el-col>
            <el-col :span="1">
              <el-button @click="del(index)" style="padding: 3px 0" type="text" icon="el-icon-delete"
                         v-if="token"></el-button>
            </el-col>
          </el-row>
        </div>
        <div style="font-size: 0.9rem;line-height: 1.5;color: #606c71;">
          最近更新 {{item.lastModifiedDate.toLocaleString()}}
        </div>
        <div style="font-size: 1.1rem;line-height: 1.5;color: #303133;padding: 10px 0px 0px 0px">
          {{item.description}}
        </div>
      </el-card>

    </div>
    <token-dialog ref="tokenDialog"></token-dialog>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import TokenDialog from '@/views/common/TokenDialog'
  import GistApi from '@/api/gist'

  export default {
    components: {
      TokenDialog
    },
    data () {
      return {
        files: [],
        submitButton: {
          loading: false,
          disabled: false
        },
        activeName: '1'
      }
    },
    computed: {
      ...mapGetters([
        'token',
      ])
    },
    created () {
      ['drop', 'dragleave', 'dragenter', 'dragover'].forEach(item => {
        document.addEventListener(item, e => e.preventDefault())
      })
    },
    methods: {
      change (e) {
        //console.log(e.target.files)
        this.push(e.target.files)
      },
      drop (e) {
        //console.log(e.dataTransfer.files)
        this.push(e.dataTransfer.files)
      },
      push (files) {
        if (files) {
          for (let i = 0; i < files.length; i++) {
            let file = files[i]
            let reader = new FileReader()
            reader.onload = e => {
              file.title = file.name.split('.').slice(0, file.name.split('.').length - 1).join('.')
              file.content = e.target.result
              file.description = file.content.replace(/#/g, '').slice(0, 300)
              //this.files.push(file)
              this.files = [file, ...this.files]
            }
            reader.readAsText(file)
          }
        }
      },
      del (fileIndex) {
        this.files.splice(fileIndex, 1)
      },
      onSubmit () {
        if (this.token) {
          this.publish()
        } else {
          this.$refs.tokenDialog.open(() => {
            this.publish()
          })
        }
      },
      publish () {
        if (this.files) {
          this.submitButton.loading = true
          this.submitButton.disabled = true
          this.files.forEach(file => {
            let form = {
              title: file.title,
              description: file.description,
              content: file.content
            }
            GistApi.create(form).then((response) => {
              let result = response.data
              // console.log(JSON.stringify(result))
              this.$message({
                message: '发表成功',
                type: 'success'
              })
              this.$router.push('/user/blog/main')
            }).then(() => {
              this.submitButton.loading = false
              this.submitButton.disabled = false
            })
          })
        }
      }
    }
  }
</script>

<style scoped>

</style>



