<template>
  <div style="height: 100%; overflow: scroll">
    <button id='start'>开始</button>
    <button id='end'>结束</button>
    <div class='container'>
      <span>canvas:</span>
      <canvas id='canvas' width='500' height='300'></canvas>
    </div>
    <div class='container'>
      <span>video:</span>
      <video id='video' width='500' height='300' src='./demo.mp4' controls></video>
      <video id='video2' width='500' height='300' autoplay controls></video>
    </div>
  </div>
</template>

<script>
  //定义两个类，Canvas以及Block
  class Canvas {
  	//blockList: Block[]
  	//canvas: any
  	//ctx: any
  	constructor (ele) {
  		this.canvas = ele
  		this.ctx = this.canvas.getContext("2d")
  		this.blockList = []
  		//绑定点击事件
  		this.canvas.addEventListener('mousedown', this.mousedownEvent.bind(this))
  		this.nowBlock = null
  	}
  	//新建一个色块
  	createBlock (option) {
  		option.Canvas = this
  		option.hierarchy = this.blockList.length
  		this.blockList.push(new Block(option))
  		this.painting()
  	}
  	//渲染一个色块
  	rendering (block) {
  		this.ctx.fillStyle = block.color
  		this.ctx.fillRect(block.x, block.y, block.w, block.h)
  	}
  	//渲染全部色块
  	painting () {
  		this.ctx.fillStyle = '#fff'
  		this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)
  		this.blockList.forEach(item => {
  			this.rendering(item)
  		})
  	}
  	//
  	mousedownEvent (e) {
  		let x = e.offsetX
  		let y = e.offsetY
  		//选择点击到的色块中层级最高的
  		this.nowBlock = this.blockList.filter(item => item.checkBoundary(x, y)).pop()
  		//没有直接退出
  		if(!this.nowBlock)	return
  		//将选择的色块的层级设置为最高
  		this.nowBlock.hierarchy = this.blockList.length
  		//重新排序
  		this.blockList.sort((a, b) => a.hierarchy-b.hierarchy)
  		//根据排序的index重新设置层级
  		this.blockList.forEach((item, index) => item.hierarchy = index)
  		//重新渲染
  		this.painting()
  		//选中的色块触发事件
  		this.nowBlock.mousedownEvent(e)
  	}
  }
  class Block {
  	//x: number
  	//y: number
  	//w: number
  	//h: number
  	//color: string
  	//Canvas: Canvas
  	//hierarchy: number
  	constructor ({x, y, w, h, color, Canvas}) {
  		this.x = x
  		this.y = y
  		this.w = w
  		this.h = h
  		this.color = color
  		this.Canvas = Canvas
  		this.hierarchy = 0
  	}
  	checkBoundary (x, y) {
  		return x>this.x && x<(this.x+this.w) && y>this.y && y<(this.y+this.h)
  	}
  	mousedownEvent (e) {
  		/* 这里 disX和disY的计算方式： e.offsetX获取到的是鼠标点击距离canvas左边的距离，this.x是色块距离canvas左边的距离。e.offsetX-this.x就是色块左边的距离。这应该很好理解了 */
  		const disX = e.offsetX - this.x // 点击时距离色块左边的距离
  		const disY = e.offsetY - this.y // 点击时距离色块上边的距离
          // 绑定鼠标滑动事件；这里mouseEvent.offsetX同样是鼠标距离canvas左侧的距离，mouseEvent.offsetX - disX就是色块的x坐标了。同理y也是这样算的。最后在重新渲染就好了。
  		document.onmousemove = (mouseEvent) => {
  			this.x = mouseEvent.offsetX - disX
  			this.y = mouseEvent.offsetY - disY
  			this.Canvas.painting()
  		}
          // 鼠标松开则清空所有事件
  		document.onmouseup = () => {
  			document.onmousemove = document.onmousedown = null
  		}
  	}
  }

  export default {
    mounted(){
      console.log('mounted')
      //进行绘画
      let mycanvas = new Canvas(document.getElementById('canvas'))
      mycanvas.createBlock({                    // 红色
      	x: 100,
      	y: 100,
      	w: 100,
      	h: 100,
      	color: '#f00'
      })
      mycanvas.createBlock({                    // 蓝色
      	x: 300,
      	y: 100,
      	w: 100,
      	h: 100,
      	color: '#00f'
      })
      mycanvas.createBlock({
      	x: 150,
      	y: 150,
      	w: 100,
      	h: 100,
      	color: '#0f0'
      })

      //获取canvas流对象
      let video2 = document.getElementById('video2')
      let canvas = document.getElementById('canvas')
      let video = document.getElementById('video')
      let start = document.getElementById('start')
      let end = document.getElementById('end')
      let canvas_stream = canvas.captureStream()
      let video_stream = video.captureStream()
      let mediaRecorder
      start.onclick = () => {
      	console.log(video_stream.getAudioTracks())
      	console.log(canvas_stream.getVideoTracks())
      	let stream = new MediaStream()
      	stream.addTrack(video_stream.getAudioTracks()[0])
      	stream.addTrack(canvas_stream.getVideoTracks()[0])
      	video2.srcObject = stream
      	mediaRecorder = new MediaRecorder(stream, {
      		audioBitsPerSecond: 128000,			//音频码率
      		videoBitsPerSecond: 100000,			//视频码率
      		mimeType: 'video/webm;codecs=h264'	//编码格式
      	})
      	mediaRecorder.ondataavailable = function (e) {
      		console.log(e)
      		//下载视频
      		let blob = new Blob([e.data], {'type': 'video/mp4'})
      		let a = document.createElement('a')
      		a.href = URL.createObjectURL(blob)
      		a.download = 'test.mp4'
      		a.click()
      	}
      	mediaRecorder.start()
      	console.log("开始采集")
      }
      end.onclick = () => {
      	mediaRecorder.stop()
      	console.log('停止采集')
      }
    }
  }
</script>

<style scoped>
  video, canvas{
    display: block;
  }
  .container{
    background: lightgray;
    padding: 10px;
  }
</style>



